  padding: 20px 20px 0;
  color: #fff;
  font-size: 17px;
}

#main {
  margin-top: 20px;
  padding-bottom: 1em;
  background-color: #fff;
}

.current-section {
  position: fixed;
  top: 0;
  left: 50%;
  width: 693px;
  margin-left: -352px;
  background: #222;
  border: 5px solid #444;
  color: #fff;
  opacity: 0;
  visibility: hidden;
  transition: opacity ease-in-out 0.3s;
  -webkit-transition: opacity ease-in-out 0.3s;
  /* Safari <=6.1, Android <= 4.3 */
}

.current-section p {
  padding: 5px 27px;
  font-size: 24px;
  font-weight: bold;
}

.current-section a {
  float: right;
  text-indent: -10000px;
  background: transparent url(../images/top.png) no-repeat 0 0;
  width: 20px;
  height: 20px;
  opacity: 0.8;
  margin-right: 12px;
  margin-top: 12px;
  opacity: 0.8;
  filter: alpha(opacity=80);
  /* IE 5-7 */
  transition: opacity ease-in-out 0.3s;
  -webkit-transition: opacity ease-in-out 0.3s;
  /* Safari <=6.1, Android <= 4.3 */
}

.current-section a:hover {
  opacity: 1;
  filter: alpha(opacity=100);
  /* IE 5-7 */
}

.current-section a.zip {
  margin-right: 8px;
}

a.zip,
a.zip > span {
  background: transparent url(../images/zip.png) no-repeat 0 0;
  width: 30px;
  height: 21px;
  display: inline-block;
  text-indent: -10000px;
  opacity: 0.8;
  filter: alpha(opacity=80);
  /* IE 5-7 */
  transition: opacity ease-in-out 0.3s;
  -webkit-transition: opacity ease-in-out 0.3s;
  /* Safari <=6.1, Android <= 4.3 */
}

