  .nav__item {
    width: 100%;
  }
}

.nav__link.nav__link.nav__link {
  display: block;
  padding: 10px 20px;
}

@supports (display: flex) {
  .nav__link.nav__link.nav__link {
    display: flex;
    align-items: center;
  }
}

@media (min-width: 481px) {
  .nav__link.nav__link.nav__link {
