.lt-ie9 .systemSwitch {
  display: none;
}

.lt-ie10 .systemSwitch {
  float: right;
}

.systemSwitch__fieldset {
  border: 0;
  padding: 0;
}

.systemSwitch__legend {
  float: left;
  margin: 0.2em 1em;
  margin-left: 0;
}

.systemSwitch__grid {
  min-width: 10em;
  border: 2px solid #63a52a;
}

.systemSwitch__label {
  display: block;
  white-space: nowrap;
  margin: 2px;
  cursor: pointer;
  font-family: Georgia, serif;
  font-style: italic;
}

.systemSwitch__text {
  text-align: center;
  transition: 0.3s;
  display: flex;
  align-items: center;
}

.systemSwitch__text::before, .systemSwitch__text::after {
  content: '';
  display: block;
  height: 1px;
  background: #315116;
  flex-grow: 1;
  margin: 0 10px;
  opacity: 0;
}

input:checked ~ .systemSwitch__text {
  font-weight: bold;
  box-shadow: inset 0 0 5px #000;
  background: #7ccc36;
  color: #000;
  border-radius: 3px;
  cursor: default;
}

input:focus ~ .systemSwitch__text::before, input:focus ~ .systemSwitch__text::after {
  opacity: 1;
}

input:not(:checked) ~ .systemSwitch__text:hover {
  background: #CDDEC4;
  box-shadow: inset 0 0 3px 2px #315116;
  color: #000;
}

