.viewGitHub {
  display: inline-flex;
  align-items: center;
}

.viewGitHub__icon {
  color: #fff;
  height: 15px;
  display: inline-flex;
  justify-content: space-between;
  align-items: center;
  margin-right: 10px;
}

.viewGitHub__icon::before, .viewGitHub__icon::after {
  content: '';
  display: inline-block;
  border: 3px solid transparent;
  height: 7px;
  width: 7px;
  transform: rotate(45deg);
  transition: 0.5s;
}

.viewGitHub__icon::before {
  border-color: transparent transparent #fff #fff;
}

.viewGitHub__icon::after {
  border-color: #fff #fff transparent transparent;
}

.viewGitHub__iconSlash {
  display: inline-block;
  transform: skew(-20deg);
  height: 100%;
  width: 3px;
  background: #fff;
  margin: 0;
  transition: 0.5s;
}

.viewGitHub:hover .viewGitHub__icon::before, .viewGitHub:focus .viewGitHub__icon::before {
  transform: rotate(45deg) translateX(-3px) translateY(3px) scale(0.8);
}

.viewGitHub:hover .viewGitHub__icon::after, .viewGitHub:focus .viewGitHub__icon::after {
  transform: rotate(45deg) translateX(3px) translateY(-3px) scale(0.8);
}

.viewGitHub:hover .viewGitHub__iconSlash, .viewGitHub:focus .viewGitHub__iconSlash {
  transform: skew(25deg) scale(0.7);
}
